/* -------------------------------------------------------------------------- */
/*                                   Nav Bar                                  */
/* -------------------------------------------------------------------------- */

.burger {
    position: relative;
    width: 40px;
    height: 30px;
    background: transparent;
    cursor: pointer;
    display: block;
}

.burger input {
    display: none;
}

.burger span {
    display: block;
    position: absolute;
    height: 4px;
    width: 100%;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
}

.burger span:nth-of-type(1) {
    top: 0px;
    transform-origin: left center;
}

.burger span:nth-of-type(2) {
    top: 50%;
    transform: translateY(-50%);
    transform-origin: left center;
}

.burger span:nth-of-type(3) {
    top: 100%;
    transform-origin: left center;
    transform: translateY(-100%);
}

.burger input:checked~span:nth-of-type(1) {
    transform: rotate(45deg);
    top: 0px;
    left: 5px;
}

.burger input:checked~span:nth-of-type(2) {
    width: 0%;
    opacity: 0;
}

.burger input:checked~span:nth-of-type(3) {
    transform: rotate(-45deg);
    top: 28px;
    left: 5px;
}

.navbar {
    position: fixed;
    z-index: 35;
}

.font {
    font-family: 'Dancing Script', cursive;
    font-size: 2.5rem;

}

/* ---------------------------------End---------------------------------------*/


/* -------------------------------------------------------------------------- */
/*                                Footer                                      */
/* -------------------------------------------------------------------------- */

.nav-footer div:nth-child(5) {

    text-align: end;

}

.nav-footer div:nth-child(5) .btn-footer {

    text-align: end;
    width: 40px;

}

.click-div{

    margin: 0;
    padding: 5px 0px;
}

.btn-footer {
    cursor: pointer;
    font-size: 1.2em;
    color: white;
    text-align: start;
    width: auto;
}

/* ---------------------------------End---------------------------------------*/


@media only screen and (min-width: 576px) {

    .absolute {
        display: block;
        position: absolute;
        top: 81vh;
        right: 2.7em;
    }

}

@media only screen and (min-width: 992px) and (max-width: 1200px) {

    .absolute {
        display: block;
        position: absolute;
        top: 82vh;
        right: 5em;
    }

}

