/* -------------------------------------------------------------------------- */
/*                                 First Page                                 */
/* -------------------------------------------------------------------------- */

.first-page {
    height: 100%;
    scroll-snap-align: start;

}

.clr-section1 {
    justify-content: center;
    align-items: center;
    background-color: #F2F2F2;
    height: 60%;
}

.font-size {
    font-size: 40px;
    font-weight: 700;
}

.clr-section2 {
    padding-bottom: 35px;
    position: relative;
    background-color: #0C3B2E;
    height: 40%;
}

.framework-brd {
    z-index: 99;
    height: 200px;
    width: 200px;
}

.photo {
    z-index: 100;
    height: 200px;
    width: 200px;

}

.img-box {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    height: 100%;
    width: 100%;
}

.svg-box {
    display: flex;
    justify-content: center;
    position: relative;
    height: 100%;
    width: 100%;
}

.svg-form {
    position: absolute;
    overflow: hidden;
    height: 240px;
    width: 350px;

}

/* ----------------------------------End---------------------------------------- */



/* -------------------------------------------------------------------------- */
/*                                 About Page                                 */
/* -------------------------------------------------------------------------- */

.about-page {
    height: 100vh;
    scroll-snap-align: start;
}


.about-font {

    text-indent: 4px;
    font-size: 18px;
    line-height: 1.8;

}

.btn-warning {
    --bs-btn-color: #000;
    --bs-btn-bg: orange;
    --bs-btn-border-color: orange;
    --bs-btn-hover-color: #000;
    --bs-btn-hover-bg: orange;
    --bs-btn-hover-border-color: orange;
    --bs-btn-focus-shadow-rgb: 217, 164, 6;
    --bs-btn-active-color: #000;
    --bs-btn-active-bg: orange;
    --bs-btn-active-border-color: orange;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #000;
    --bs-btn-disabled-bg: orange;
    --bs-btn-disabled-border-color: orange;

}

/* ----------------------------------End---------------------------------------- */




/* -------------------------------------------------------------------------- */
/*                                Contact Page                                */
/* -------------------------------------------------------------------------- */

.contact-page {
    height: 100vh;
    width: 100%;
    scroll-snap-align: start;
}

.form-heading {
    font-size: 30px;
}

.form-text {
    font-size: 18px
}

.input-frm {
    border: 2px solid;
    border-radius: .375rem !important;
    padding: 10px 10px;
}

.input-section {
    font-size: 1rem;
    line-height: 1.25rem;
    margin-top: 0.25rem;
}

.btn-warning {
    height: 50px;
    width: 100%;
}

/* ----------------------------------End---------------------------------------- */



@media only screen and (min-width: 576px) and (max-width: 768px) {

    .clr-section1 {
        height: 100vh;
    }

    .clr-section2 {
        height: 100vh;
    }

    .font-size {
        font-size: 30px;
    }

    .framework-brd {
        height: 250px;
        width: 250px;
    }

    .photo {
        height: 250px;
        width: 250px;
    }

    .svg-form {
        width: 390px;
        height: 620px;
    }

}

@media only screen and (min-width: 768px) {

    .clr-section1 {
        height: 100vh;
    }

    .clr-section2 {
        height: 100%;
    }

    .font-size {
        font-size: 40px;
    }

    .framework-brd {
        height: 280px;
        width: 280px;
    }

    .photo {
        height: 280px;
        width: 280px;
    }

    .svg-form {
        width: 450px;
        height: 600px;
    }

    .about-font {

        text-indent: 4px;
        font-size: 20px;
        line-height: 1.9;

    }

    .form-heading {
        font-size: 35px;
    }

    .form-text {
        font-size: 20px
    }

    .btn-warning {
        width: 200px;
    }

}

@media only screen and (min-width: 992px) {

    .font-size {
        font-size: 50px;
    }

    .framework-brd {
        height: 300px;
        width: 300px;
    }

    .photo {
        height: 300px;
        width: 300px;
    }

    .svg-form {
        width: 620px;
        height: 680px;
    }

    .about-font {
        font-size: 26px;
    }


}

@media only screen and (min-width: 1200px) {

    .framework-brd {
        height: 320px;
        width: 320px;
    }

    .photo {
        height: 320px;
        width: 320px;

    }

    .about-font {
        font-size: 28px;
    }

}

